import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ParentDashboardComponent } from './parent-dashboard/parent-dashboard.component';
import { LayoutModule } from '@angular/cdk/layout';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { NavigationComponent } from './navigation/navigation.component';
import { MatFormFieldModule, MatDatepickerModule, 
  MatToolbarModule, MatButtonModule, MatSidenavModule, MatListModule, MatExpansionModule } from '@angular/material';
 import { MatNativeDateModule, MatCheckboxModule, MatGridListModule,MatCardModule,
MatInputModule,MatDialogModule,MatTableModule,MatMenuModule,
  MatIconModule, MatSelectModule} from '@angular/material'
import { ParentProfileComponent } from './parent-profile/parent-profile.component';
import { ChildrenViewComponent } from './children-view/children-view.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ChildFormComponent } from './child-form/child-form.component';

const routes: Routes = [
  { path: 'login', component: LoginComponent},
  { path: 'dashboard', component: NavigationComponent, 
          children: [
            { path: 'children', component: ChildrenViewComponent,
            
                    children: [
                      { path: 'addChild', component: ChildFormComponent},
                    ] 
            },
            { path: 'addChild', component: ChildFormComponent},
            { path: 'profile', component: ParentProfileComponent},
            { path: 'register', component: ParentDashboardComponent},
            { path: '', redirectTo: 'profile', pathMatch: 'full' }
          ] 
        
  },
  { path: '', redirectTo: 'login', pathMatch: 'full' },
]


@NgModule({
  declarations: [
    AppComponent,
    ParentDashboardComponent,
    LoginComponent,
    NavigationComponent,
    ParentProfileComponent,
    ChildrenViewComponent,
    ChildFormComponent,
  ],
  imports: [
    MatExpansionModule,
    BrowserModule,
    BrowserAnimationsModule,
    MatSelectModule,
    MatNativeDateModule, 
    MatCheckboxModule, 
    MatGridListModule,
    MatCardModule,
    MatInputModule,
    MatDialogModule,
    MatTableModule,
    MatMenuModule,
    // MyOwnCustomMaterialModule,
    LayoutModule,
    RouterModule.forRoot(routes),
    MatToolbarModule,
    MatButtonModule,
    MatSidenavModule,
    MatIconModule,
    FormsModule,
    ReactiveFormsModule,
    MatFormFieldModule,
    MatListModule,
    MatDatepickerModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
